@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Hind:wght@300;400;500;600;700&display=swap);
* {
  outline: none;
  text-decoration: none;
}

body {
  text-shadow: -2px 2px 2px rgb(0 0 0 / 15%);
  font-family: Fredoka One;
  overflow-x: hidden;
  margin: 0 !important;
}

.App {
  background-image: var(--clr-secondary-bck);

  width: 100%;
}

.toggle {
  position: fixed;
  width: 100px;
  height: auto;
  margin-top: 20px;
  background-color: transparent;
  background: transparent;
  border: 0;
  z-index: 2000;
}

::-webkit-scrollbar {
  width: 3px !important;
  height: 3px !important;
}

body ::-webkit-scrollbar-thumb {
  background-color: var(--clr-primary-bck);
}

::-webkit-scrollbar-thumb {
  background-color: var(--clr-primary-bck);
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background-color: var(--clr-terciary-bck);
}

:root {
  --clr-primary-txt: #2e2e2e;
  --clr-secondary-txt: #18cf37;

  --clr-primary-bck: #ffffff;
  --clr-secondary-bck: linear-gradient(315deg, #5eff6e 0%, #49d44580 74%);
  --gradient-overlay: linear-gradient(
    150deg,
    rgba(155, 255, 125, 0.508) 20%,
    rgba(129, 255, 255, 0.438) 80%
  );

  --overlay-yt: linear-gradient(
    90deg,
    rgba(255, 0, 0, 0) 20%,
    rgba(92, 0, 0, 0) 80%
  );
  --yt-bck: #ce0b0b;

  --clr-primary-btn-txt: #ffffff;
  --clr-secondary-btn-txt: #818181;

  --clr-primary-btn-bck: #1d1d1d;
  --clr-secondary-btn-bck: #000000;

  --red-msg: #e90a1d;
  --green-msg: #0c8025;

  --shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  --shadow2: rgba(0, 0, 0, 0.205) 0px 5px 10px;

  --font-xl: 90px;
  --font-large: 30px;
  --font-medium: 20px;
  --font-small: 12px;

  --spacing: 10px;
}

[data-theme="dark"] {
  --clr-primary-txt: rgb(245, 245, 245);
  --clr-secondary-txt: #005f10;

  --clr-primary-bck: #f10000;
  --clr-secondary-bck: linear-gradient(315deg, #005050 0%, #10013a 74%);

  --gradient-overlay: linear-gradient(
    315deg,
    rgba(31, 31, 31, 0.5) 0%,
    rgba(26, 26, 26, 0.493) 74%
  );

  --yt-bck: #ffffff;

  --overlay-yt: linear-gradient(
    90deg,
    rgba(65, 65, 65, 0.514) 20%,
    rgba(116, 3, 3, 0.459) 80%
  );

  --clr-primary-btn-txt: #ffffff;
  --clr-secondary-btn-txt: #818181;

  --clr-primary-btn-bck: #1d1d1d;
  --clr-secondary-btn-bck: #000000;

  --red-msg: #e90a1d;
  --green-msg: #0c8025;

  --shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

  --font-xl: 90px;
  --font-large: 30px;
  --font-medium: 20px;
  --font-small: 12px;

  --spacing: 10px;
}

@media screen and (max-width: 1000px) {
  :root {
    --font-large: 25px;
    --font-xl: 70px;
  }

  [data-theme="dark"] {
    --font-large: 25px;
    --font-xl: 70px;
  }
}

@media screen and (max-width: 514px) {
  :root {
    --font-xl: 30px;
    --font-large: 25px;
    --font-medium: 20px;
  }

  [data-theme="dark"] {
    --font-xl: 30px;
    --font-large: 25px;
    --font-medium: 20px;
  }
}

.grid-container {
  margin-top: 2rem;
  width: -webkit-fill-available;
  height: 80%;
  place-items: center;
  display: flex;
  justify-content: center;
  max-width: 1400px;
}

.info-content {
  width: 80%;
  height: 80%;
  justify-content: center;
  display: grid;
  align-content: center;
  grid-template-columns: [first] 20% [line2] 24% [line3] 24% [col4-start] 14% [five] 18% [end];
  grid-template-rows: [row1-start] 170px [row1-end] 130px [third-line] 130px [four-line] 130px [last-line];
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}

.info-vmario-container {
  grid-column-start: 2;
  grid-column-end: 6;
  grid-row-start: row1-start;
  grid-row-end: 1;
}

.vmario-container {
  grid-column-start: 1;
  grid-column-end: 1;
  grid-row-start: 1;
  grid-row-end: 5;
}

.first-channel-container {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 4;
}

.second-channel-container {
  grid-column-start: 3;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 4;
}

.instagram-container {
  grid-column-start: 4;
  grid-column-end: 6;
  grid-row-start: 2;
  grid-row-end: 2/2;
}

.twitch-container {
  grid-column-start: 4;
  grid-column-end: 6;
  grid-row-start: 3;
  grid-row-end: 2/2;
}

.twitter-container {
  grid-column-start: 4;
  grid-column-end: 6;
  grid-row-start: 4;
  grid-row-end: 2/2;
}

.nauticmc-container {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 4;
  grid-row-end: 2/2;
}

.tiktok-container {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 4;
  grid-row-end: 2/2;
}

/* Hover effects */

.first-channel-container,
.second-channel-container,
.instagram-container,
.twitch-container,
.twitter-container,
.nauticmc-container,
.tiktok-container {
  transition: all 0.7s;
}

.first-channel-container:hover,
.second-channel-container:hover,
.instagram-container:hover,
.twitch-container:hover,
.twitter-container:hover,
.nauticmc-container:hover,
.tiktok-container:hover {
  -webkit-transform: scale(0.97);
          transform: scale(0.97);
  cursor: pointer;
}

@media screen and (max-width: 767px) {
  .grid-container {
    padding: 0 25px 0px 25px;
  }
  .vmario-container {
    display: none !important;
  }

  .info-vmario-container {
    grid-column-start: 1;
  }

  .first-channel-container {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 2;
  }

  .second-channel-container {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 3;
    grid-row-end: 3;
  }

  .nauticmc-container {
    grid-column-start: 1;
    grid-column-end: 2;
  }

  .tiktok-container {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 4;
    grid-row-end: 2/2;
  }
}

@media screen and (max-width: 683px) {
  .grid-container {
    padding: 0 20px 0px 20px;
  }

  .info-content {
    padding: 1rem;
    width: 100%;
    grid-template-columns: [first] 20% [line2] 20% [line3] 20% [col4-start] 20% [five] 20% [end];
    grid-template-rows: [row1-start] 120px [row1-end] 120px [third-line] 120px [four-line] 120px [five-line] 120px [six-line] 120px [seven-line] 120px [eight-line] 120px [last-line];
  }

  .info-vmario-container {
    grid-column-start: 1;
  }

  .first-channel-container {
    grid-column-end: 6;
  }

  .second-channel-container {
    grid-column-start: 1;
    grid-column-end: 6;
    grid-row-start: 3;
    grid-row-end: 3;
  }

  .nauticmc-container {
    grid-column-end: 6;
  }

  .instagram-container {
    grid-column-start: 1;
    grid-column-end: 6;
    grid-row-start: 5;
    grid-row-end: 5;
  }

  .twitch-container {
    grid-column-start: 1;
    grid-column-end: 6;
    grid-row-start: 6;
    grid-row-end: 2/2;
  }

  .twitter-container {
    grid-column-start: 1;
    grid-column-end: 6;
    grid-row-start: 7;
    grid-row-end: 2/2;
  }

  .tiktok-container {
    grid-column-start: 1;
    grid-column-end: 6;
    grid-row-start: 8;
    grid-row-end: 2/2;
  }
}

